      flex-wrap: wrap;
      justify-content: center;
      margin-right: 1vw;
      background-color: #fff; }
      .contents--container .contents .contents--columns .column1 .column1--title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px;
        width: 90%;
        border-bottom: 1px solid #DDD; }
        .contents--container .contents .contents--columns .column1 .column1--title .title-text {
          display: flex;
          color: black;
          font-style: normal;
          font-size: 1.1em; }
        .contents--container .contents .contents--columns .column1 .column1--title .title-popnewlinks {
