          font-size: 0.7em; }
          .contents--container .contents .contents--columns .column1 .column1--title .title-popnewlinks .poplink {
            margin-right: 10px; }
            .contents--container .contents .contents--columns .column1 .column1--title .title-popnewlinks .poplink:hover {
              color: black; }
          .contents--container .contents .contents--columns .column1 .column1--title .title-popnewlinks .newlink:hover {
            color: black; }
      .contents--container .contents .contents--columns .column1 .column1--rows {
        display: flex;
