          .contents--container .contents .contents--columns .column1 .column1--rows .column1--row .product-image {
            width: 50px;
            height: 50px; }
          .contents--container .contents .contents--columns .column1 .column1--rows .column1--row .description {
            margin-left: 1vw; }
          .contents--container .contents .contents--columns .column1 .column1--rows .column1--row .title {
            color: color-grey-text;
            font-style: normal;
            font-size: 1.4em; }
          .contents--container .contents .contents--columns .column1 .column1--rows .column1--row .details {
            color: color-grey-text;
            font-style: normal;
            font-size: 0.9em; }
        .contents--container .contents .contents--columns .column1 .column1--rows .column1-row1 {
          margin-top: 1vh; }
    .contents--container .contents .contents--columns .column2 {
      display: flex;
      flex-grow: 1;
      flex-wrap: wrap; }
      .contents--container .contents .contents--columns .column2 .kitbit {
        background-color: #fff; }
        .contents--container .contents .contents--columns .column2 .kitbit .kitbit--close-btn {
          display: flex;
          margin-left: auto; }
          .contents--container .contents .contents--columns .column2 .kitbit .kitbit--close-btn svg:hover .closebtn-path {
            fill: black; }
          .contents--container .contents .contents--columns .column2 .kitbit .kitbit--close-btn svg .closebtn-path {
            fill: #ADADAD; }
        .contents--container .contents .contents--columns .column2 .kitbit .title {
          color: color-grey-text;
          font-style: normal;
          font-size: 0.9em; }
